import React from 'react';

class About extends React.Component
{
	render(){
		return(
			<div>
        <h1>Hello,</h1>
        <div className="subheading mb-5">I'M &#934;sal</div>
        <p className="mb-5">
        	I design and develop software in various platforms (HTML5, React, Angular, Unity – Andriod/PC/Web) and I enjoy the challenge of creating engaging solutions and achieving the results-driven objectives.
        	I also worked in electronics, embedded software and designing data-driven solutions using machine learning.
        </p>
        
        <p>Nowadays I get my hands dirty with both frontend and backend development and like to ponder stuff like coding standards, design methodologies, usability, and accessibility. I'm a bit of an idealist sometimes, but try to balance that with a healthy dose of pragmatism.</p>
				
				<p className="my-5">THE ONLY TIME YOU SHOULD EVER LOOK BACK, IS TO SEE HOW FAR YOU'VE COME<i className="material-icons">format_quote</i></p>
      </div>
		)
	}
}

export default About;


